
.slider {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  width: 100%;
  /* Full-width */
  height: 2px;
  /* Specified height */
  background: #d3d3d3;
  /* Grey background */
  outline: none;
  /* Remove outline */
  opacity: 1;
  /* Set transparency (for mouse-over effects on hover) */
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.slider:hover {
    opacity: 1;
    /* Fully shown on mouse-over */
}
.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    /* Override default look */
    appearance: none;
    width: 20px;
    /* Set a specific slider handle width */
    height: 20px;
    /* Slider handle height */
    border-radius: 100%;
    background-color: #eee;
    border: 4px solid #41b987;
    /* Green background */
    cursor: pointer;
    /* Cursor on hover */
    position: relative;
    opacity: 1;
}



